$primary-color: #2af598;
$secondary-color: #00715f;
$tertiary-color: #33b072;
$quaternary-color: #95b9b0;
$background-color: #262f2aec;
$text-color: #fafff0;
$font-color: #a2aa94;

.timeline {
  position: relative;
  padding: 20px 0;
  margin-left: 10px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  @media (max-width: 768px) {
    margin-left: 20px;
  }

  @media (max-width: 430px) {
    margin-left: 15px;
    padding: 10px 0;
  }
}

.timeline-item {
  position: relative;
  margin-right: 20px;
  margin-bottom: 30px;
  padding-left: 20px;
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0) 0%,
    rgba(245, 245, 245, 0.1) 200%
  );
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  position: relative;
  transform-origin: left center;

  &:hover {
    background: linear-gradient(
      90deg,
      rgba(255, 255, 255, 0.1) 0%,
      rgba(245, 245, 245, 0.2) 200%
    );
    transform: translateY(-5px);
    transition:
      transform 0.3s ease-in-out,
      background 0.3s ease-in-out;
  }

  .timeline-content {
    padding-left: 10px;
    margin-right: 20px;
    color: $text-color;
    border-left: 2px solid $primary-color;

    h3 {
      font-size: 1.5em;
      margin-bottom: 3px;
      color: darken($primary-color, 20%);
      transition: color 0.3s ease-in-out;

      @media (max-width: 768px) {
        font-size: 1.3em;
      }

      @media (max-width: 430px) {
        font-size: 1.1em;
      }
    }

    p {
      font-size: 1em;
      color: lighten($font-color, 30%);

      @media (max-width: 768px) {
        font-size: 0.9em;
      }

      @media (max-width: 430px) {
        font-size: 0.8em;
      }
    }
  }

  .timeline-company {
    color: lighten($secondary-color, 10%);
  }
  .timeline-institution {
    color: lighten($secondary-color, 10%);
  }

  .timeline-atsign {
    color: $text-color;
  }

  .timeline-date {
    margin-top: 5px;
    font-size: 0.9em;
    color: $quaternary-color;
  }
}

.timeline-step {
  position: absolute;
  left: -12px;
  top: 0;
  height: 100%;
  display: flex;
  align-items: center;
}

.timeline-section {
  margin-left: 30px;
  margin-right: 30px;
}

.timeline-section-title {
  font-size: 2em;
  color: $text-color;
  margin-bottom: 15px;
  padding-left: 10px;
  font-weight: bold;

  @media (max-width: 768px) {
    font-size: 1.7em;
  }

  @media (max-width: 430px) {
    font-size: 1.4em;
  }
}

.timeline-stats {
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;

  .stat-item {
    font-size: 0.9em;
    color: $text-color;
    margin-bottom: 5px;

    strong {
      color: $primary-color;
    }
  }
}
